/* Quiz Component */
.answerOptions {
  margin: 0;
  padding: 0;
  list-style: none;
}
 
/* QuestionCount Component */
.questionCount {
  padding: 1.5rem 2.5rem 0;
  font-size: 14px;
}
 
/* Question Component */
.question {
  margin: 0;
